















































































































import uniSegmentedControl from "@/components/uni-segmented-control.vue";
import helper from '../../common/helper.js';
export default {
	components: {
		uniSegmentedControl
	},
	data() {
		return {
			fenleiArray: {}, //分类列表菜单
			fen: [],
			fen1: [],
			current: 0 //分类列表菜单索引从0开始
		}
	},
	onLoad() {
		this.rwdata();
		this.rwflList();
	},
	onPullDownRefresh: function() { //下拉刷新
		this.rwdata(true);
	},
	methods: {
		onClickItem(index) { //分类列表点击事件
			if (this.current !== index) {
				this.current = index;
			}
		},
		// 请求任务分类
		rwdata(isdown) {
			uni.request({
				url: helper.websiteUrl + 'task/getcategory',
				method: 'GET',
				data: helper.postdata({}),
				success: (res) => {
					console.log(res)
					if (res.data.code == 200) {
						this.fenleiArray = JSON.parse(JSON.stringify(res.data.data.baseinfo));
						for (var i = 0; i < this.fenleiArray.length; i++) {
							this.fen.push(this.fenleiArray[i].Name);
							this.fen1.push(this.fenleiArray[i].ID);
							
						}
						console.log(this.fen)
						console.log(this.fen1)
					} else {
						helper.goout(res.data.info);
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
					}
				},
				fail: () => {
					uni.hideLoading();
					uni.showToast({
						icon: 'none',
						title: '网络异常,请下拉刷新重试！'
					});
				},
				complete: () => {
					if (isdown) {
						uni.stopPullDownRefresh(); //刷新停止
					}
				}
			});
		},
		// 分类任务列表数据
		rwflList() {
			helper.islogin(true);
			uni.showLoading({
				title: "加载中....",
				mask: true
			})
			uni.request({
				url: helper.websiteUrl + 'task/gettasklist',
				method: 'GET',
				data: helper.postdata({
					"page":"1",
					"num":"20",
					"category":"1"
				}),
				success: res => {
					console.log(res,1)
					uni.hideLoading();
				},
				fail: () => {},
				complete: () => {}
			});
		}
	}
}



